.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
  font-family: 'Lora', serif;
  font-weight: 500;
  font-size: 30px;
  color: var(--title);
  margin: 40px 0 30px 0;
}



p {
  font-family: 'Lora', serif;
  font-size: 20px;
  font-weight: normal;
  line-height: 1.8em;
  color: var(--title);
  margin-bottom: 20px;
}

table {
  margin: 0 auto;
}

img {
  margin: 0 auto;
}

a {
  color: var(--light-blue);
}